<template>
  <div>
    <div class="main_top">
      <img src="~@/assets/imgs/mainlogo.jpg" alt="" />
    </div>
    <div class="main_top_left" v-show="isShow">
      <div class="container">
        <div class="menu_option">
          <img src="~@/assets/imgs/bt0101.gif" alt="" />
          <img
            src="~@/assets/imgs/bt0202.gif"
            @click="showEvent(false)"
            alt=""
          />
        </div>
        <div class="key_option" v-show="!isShow">
          <img
            src="~@/assets/imgs/bt0101.gif"
            @click="showEvent(true)"
            alt=""
          />
          <img src="~@/assets/imgs/bt0202.gif" alt="" />
        </div>
        <span
          class="el-icon-s-fold"
          @click="collapseEvent"
          v-show="isCollapse"
        ></span>
        <span
          class="el-icon-s-unfold"
          @click="collapseEvent"
          v-show="!isCollapse"
        ></span>
        <div class="main_top_right">
          <span> &nbsp;&nbsp;南阳华益&nbsp;&nbsp;</span>|
          <span>&nbsp;&nbsp;供应链管理系统&nbsp;&nbsp;</span>|
          <span v-show="dzyname ? true : false"
            >&nbsp;&nbsp;{{ dzyname }}，你好！</span
          >
        </div>
      </div>
      <div class="main_top_right_r">
        <span @click="$router.replace('/DataCenter')">主页&nbsp;&nbsp;</span>|
        <span>&nbsp;&nbsp;快捷键设置&nbsp;&nbsp;</span>|
        <span>&nbsp;&nbsp;关于&nbsp;&nbsp;</span>|
        <span>&nbsp;&nbsp;注销&nbsp;&nbsp;</span>|
        <el-popconfirm
          confirm-button-text="确定"
          cancel-button-text="取消"
          icon="el-icon-switch-button"
          icon-color="red"
          title=" 确定退出登录吗？"
          @confirm="back"
        >
          <span slot="reference"
            ><span>&nbsp;&nbsp;退出&nbsp;&nbsp;&nbsp;&nbsp; </span></span
          >
        </el-popconfirm>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  props: ["isCollapse", "isShow"],
  data() {
    return {};
  },
  computed: {
    dzyname() {
      return this.$store.state.dzyname;
    },
  },
  methods: {
    ...mapMutations(["getUsername", "getPassword", "getdzyname"]),
    // 退出
    back() {
      localStorage.clear();
      this.getUsername("");
      this.getPassword("");
      this.getdzyname("");
      this.$router.replace("/");
    },
    // 切换按钮
    showEvent(bol) {
      // this.$emit("isShowEvent", bol);
    },
    // 是否折叠左边导航
    collapseEvent() {
      this.$emit("collapse");
    },
  },
};
</script>

<style lang="less" scoped>
.main_top {
  img {
    padding: 20px 0 10px 20px;
    width: 130px;
    height: 20px;
  }
}
.main_top_left {
  width: 100%;
  background-color: rgb(81, 123, 234);
  display: flex;
  justify-content: space-between;
  .container {
    display: flex;
    cursor: pointer;
    .el-icon-s-fold,
    .el-icon-s-unfold {
      font-size: 18px;
      text-align: center;
      margin-left: 5px;
      color: white;
      cursor: pointer;
    }
    .main_top_right {
      text-align: center;
      font-size: 13px;
      color: white;
    }
  }
  .main_top_right_r {
    text-align: center;
    font-size: 13px;
    color: white;
    span {
      cursor: pointer;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
</style>
